@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
*{padding:0;margin:0;box-sizing:border-box;font-family:"Poppins",sans-serif}li{display:list-item;list-style:none}li::marker{color:black}a{color:#000;text-decoration:none !important}article,aside,details,figcaption,figure,footer,header,main,menu,nav,section{display:block}.clearfix::after{clear:both;content:"";display:table}.container-app{width:100%;min-height:100vh;position:relative;display:flex !important}.container{padding-top:2%}#menu{width:320px;height:100vh;padding:2%;position:-webkit-sticky;position:sticky;background:#232e3d;overflow-y:auto;flex-shrink:0;top:0%;display:none}#menu .logo{width:150px;height:44px}#menu #sidebar{height:100%;display:block}#menu #sidebar h1{margin-top:20px !important;font-size:22px}#menu #sidebar h1 a{color:#c8e1ff;display:block}#menu #sidebar ul{padding:0 !important}#menu #sidebar ul li{margin:8px 0}#menu #sidebar ul a{display:block;line-height:1.8;color:#c8e1ff;opacity:0.8;font-size:16px !important}#menu #sidebar .sublist{margin-left:5px}#menu #sidebar .sublist a{font-size:14px !important}#sidebar ul li a:hover{background:#1f2936}#sidebar ul li.active>a,a[aria-expanded="true"]{color:#fff;background:#1f2936}a[data-toggle="collapse"]{position:relative}.dropdown-toggle::after{display:block;position:absolute;top:50%;right:20px;transform:translateY(-50%)}footer{display:flex;flex-direction:column;height:12vh;justify-content:center;text-align:center;background-color:#232e3d}footer p{color:#fff;font-size:14px}.index{width:100%;min-height:100vh}.index header{display:grid;height:10vh;align-items:center;padding:10px;grid-template-columns:65% 35%}.index header .main-logo{width:130px}.index .search{background:#eeeeee;width:100%;height:35vh;background-position:center;background-repeat:no-repeat;display:flex;flex-direction:column;justify-content:center;padding:5%}.index .search h1{font-size:38px}.index .search form img{width:25px}.index .search form input{margin-top:16px;min-width:50px;height:38px;padding:8px 0 8px 30px;font-size:16px;border-radius:5px;background-repeat:no-repeat;background-size:20px;background-position:left 8px center;border:none;box-shadow:0 0 2px black}.index main{padding:5%}.index main h1{font-size:28px;margin-bottom:20px}.index main .main-content{display:flex;flex-wrap:wrap;perspective:1000px;width:100%;justify-content:center}.index main .main-content .box{width:80%;min-width:160px;max-width:200px;height:100px;display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center;box-shadow:0 0 2px black;margin:8px;border-radius:5px;transition:transform 0.4s}.index main .main-content .box a{display:block;width:100%;height:100%;display:flex;align-items:center;justify-content:center}.index main .main-content .box:hover{transform:scale(1.06)}@media all and (min-width: 400px){.index header{grid-template-columns:80% 20% !important}}@media all and (min-width: 768px){.index header{grid-template-columns:85% 15% !important}.search input{min-width:300px !important}}@media all and (min-width: 1024px){.search input{min-width:360px !important}#menu{display:block !important}.main-content{justify-content:left !important}.box a{font-size:16px}}

