* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: $text-font-stack;
}

//basic style for links

li {
  display: list-item;
  list-style: none;
}

li::marker {
  color: black;
}

a {
  color: $brand-color;
  text-decoration: none !important;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section {
  display: block;
}

// Breakpoints map
/// @prop {string} keys
/// @prop {Map} values
$breakpoints: (
  "small": 260px,
  "medium": 768px,
  "large": 1024px,
) !default;
